export default function Task(genObj) {
    this.genObj = genObj;
    this.cancelled = false;
    this.done = false;
    this.thrown = false;
    this.value = undefined;
    this.callback = null;
    this.valuesLeft = 0;
}

Task.prototype = {
    isRunning: function isRunning() {
        return !this.done;
    },
    whenDone: function whenDone() {
        var _this = this;

        if (this.done) {
            return Promise.resolve(this);
        }

        return new Promise(function (resolve) {
            _this.callback = function () {
                resolve(_this);
            };
        });
    },
    cancel: function cancel() {
        this.cancelled = true;
        this.done = true;
        if (this.child) {
            this.child.cancel();
        }
    }
};